export default {
	data() {
		return {
		}
	},
	methods: {
		goCode() {
			uni.scanCode({
				success: res => {
					console.log('条码类型：' + res.scanType);
					console.log('条码内容：' + res.result);
				}
			});
		},
		goSearch() {
			// 进入搜索页
			uni.navigateTo({
				url: `/pages/search`
			})
		},
		goMe() {
			// 进入个人中心
			uni.switchTab({
				url: `/pages/me`
			})
		}
	}
}
